import { CiSearch } from "react-icons/ci";
import { useForm } from "react-hook-form";
import { useState } from "react";
import { useNavigate } from "react-router-dom";

interface IForm {
  guildName: string;
  worldName: string;
}

function GuildItem() {
  const { register, handleSubmit, watch } = useForm<IForm>();
  const guildName = watch("guildName");
  const worldName = watch("worldName");
  const navigate = useNavigate();

  const onSearch = () => {
    if (guildName.trim() === "" || worldName.trim() === "") return;
    navigate(`/guild/search?worldName=${worldName}&guildName=${guildName}`);
  };
  return (
    <div className="mx-auto max-w-[1200px] py-10 px-10 text-center">
      <h2 className="mb-5 font-bold text-2xl"></h2>
      <div className="inline-block">
        <form
          onSubmit={handleSubmit(onSearch)}
          className="flex items-center bg-white rounded-xl border-2 border-gray-100 shadow-md focus-within:border-orange-400 transition-all duration-300 flex-col md:flex-row"
        >
          <select className="block w-64 md:w-32  rounded-lg bg-white px-3 py-2 text-sm text-gray-700 focus:border-blue-500 focus:ring-1 focus:ring-blue-500"
            {...register("worldName", { required: true })}>
            <option value="">--</option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value="2">2</option>
            <option value="3">3</option>
            <option value="4">4</option>
          </select>
          <div className="flex flex-row">
            <input
              type="text"
              {...register("guildName", { required: true })}
              placeholder=" ."
              className="w-64 px-4 py-3 rounded-l-xl bg-transparent text-[16px] focus:outline-none placeholder:text-gray-400"
            />
            <button
              type="submit"
              className="w-[52px] h-[52px] flex items-center justify-center bg-blue-500 hover:bg-blue-400 active:scale-95 rounded-r-xl transition-all duration-200"
            >
              <CiSearch className="text-white text-2xl" />
            </button>
          </div>
        </form>
      </div>
    </div>
  )
}
export default GuildItem;